import React, { useEffect, useState } from "react";
import {
  Card,
  CardBody,
  CardTitle,
  CardText,
  Button,
  Row,
  Col,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from "reactstrap";
import { useNavigate } from "react-router-dom";
import { getAllTodos, deleteTodo } from "../services/api"; // API 호출 메서드 임포트

// 컬러
const colors = ["info", "success", "warning", "danger", "primary"];

// 랜덤 컬러 생성 함수
const getRandomColor = () => colors[Math.floor(Math.random() * colors.length)];

const ToDoList = () => {
  const [todos, setTodos] = useState([]); // ToDo 데이터 초기화
  const [selectedTodo, setSelectedTodo] = useState(null); // 삭제할 ToDo 데이터
  const [isModalOpen, setIsModalOpen] = useState(false); // 모달 열림 상태
  const navigate = useNavigate();

  // 서버에서 ToDo 목록 가져오기
  const fetchTodos = async () => {
    try {
      const response = await getAllTodos(); // API 호출
      setTodos(response.data);
    } catch (error) {
      console.error("Error fetching ToDo list:", error);
    }
  };

  useEffect(() => {
    fetchTodos();
  }, []);

  // 상세 보기로 이동
  const handleViewDetails = (id) => {
    navigate(`/todo_detail/${id}`);
  };

  // 모달 열기
  const openDeleteModal = (todo) => {
    setSelectedTodo(todo);
    setIsModalOpen(true);
  };

  // 모달 닫기
  const closeDeleteModal = () => {
    setSelectedTodo(null);
    setIsModalOpen(false);
  };

  // ToDo 삭제
  const handleDelete = async () => {
    if (!selectedTodo) return;
    try {
      await deleteTodo(selectedTodo.id); // API 호출
      setTodos(todos.filter((todo) => todo.id !== selectedTodo.id)); // UI 업데이트
    } catch (error) {
      console.error("Error deleting ToDo:", error);
    } finally {
      closeDeleteModal(); // 모달 닫기
    }
  };

  return (
    <div>
      {todos.length === 0 ? (
        // ToDo 데이터가 없을 때 표시
        <div className="text-center mt-4">
          <h6>일정이 없습니다.</h6>
          <p>시작하려면 새로운 ToDo를 추가하세요!</p>
        </div>
      ) : (
        // ToDo 데이터가 있을 때 카드 렌더링
        <Row>
          {todos.map((todo) => (
            <Col md="6" lg="3" key={todo.id}>
              <Card body color={getRandomColor()} inverse>
                <CardBody>
                  <CardTitle tag="h5">{todo.title}</CardTitle>
                  <CardText>Due Date: {todo.date}</CardText>
                  <div className="d-flex justify-content-between">
                    <Button
                      color="light"
                      onClick={() => handleViewDetails(todo.id)}
                    >
                      View
                    </Button>
                    <Button
                      color="light-danger"
                      onClick={() => openDeleteModal(todo)}
                    >
                      Delete
                    </Button>
                  </div>
                </CardBody>
              </Card>
            </Col>
          ))}
        </Row>
      )}

      {/* Delete Confirmation Modal */}
      <Modal isOpen={isModalOpen} toggle={closeDeleteModal}>
        <ModalHeader toggle={closeDeleteModal}>Confirm Delete</ModalHeader>
        <ModalBody>
          이 일정을 삭제하시겠습니까:{" "}
          <strong>{selectedTodo?.title}</strong>?
        </ModalBody>
        <ModalFooter>
          <Button color="danger" onClick={handleDelete}>
            Yes, Delete
          </Button>
          <Button color="secondary" onClick={closeDeleteModal}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
    </div>
  );
};

export default ToDoList;
